.frame{
    background-color: #282c34;
    width: 400px;
    height: 600px;
    border-radius: 4px;
    padding: 10px;
    margin: 10px;
    color: #f2f2f2;
    h1{
        padding: 0px;
        margin: 4px;
        text-align: left;
    }
    .list::-webkit-scrollbar {
        display: none;
    }
    .insertNew{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        margin: auto;
        margin-top: 15px;
        // border: 2px solid blue;
        .newButton{
            border-radius: 4px;
            color: #486966;
            background-color: #f2f2f2;
            // border: 2px solid #486966;
            // height: 40px;
            padding: 10px;
            width: 200px;
            vertical-align: middle;
        }
    }
    .list{
        // border: 2px solid orangered;
        height: 80%;
        overflow-y: scroll;
        scrollbar-width: 0px;
        .item{
            height: min-content;
            background-color: white;
            width: 95%;
            margin: auto;
            border-radius: 4px;
            margin-bottom: 10px;
            display: flex;
            flex-direction: column;
            padding: 10px;
            h3{
                margin: 0;
                color: #282c34;
                text-align: left;
                
            }
            .titleRow{
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                h3{
                    margin: 0;
                    color: #282c34;
                    text-align: left;
                    
                }
            }
            p{
                margin: 0;
                color: #282c34;
                text-align: left;
                margin-bottom: 10px;
            }
            .buttonRow{
                width: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-evenly;
                .clearButton{
                    vertical-align: middle;
                    padding-top: 5px;
                    width: 120px;
                    height: 30px;
                    border: 2px solid #889C9B;
                    // background-color: green;
                    color: #889C9B;
                    border-radius: 4px;
                }
                .snoozeButton{
                    padding-top: 5px;
                    width: 120px;
                    height: 30px;
                    border: 2px solid #BD2A2E;
                    color: #BD2A2E;
                    border-radius: 4px;
                }

            }
            
        }
    }
}